@import './utils.scss';


* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;

}

body {
	background: rgb(2,0,36);
	background: -webkit-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(143,235,254,1) 0%, rgba(83,83,235,1) 100%);
	background: -o-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(143,235,254,1) 0%, rgba(83,83,235,1) 100%);
	background: linear-gradient(to right, rgba(2,0,36,1) 0%, rgba(143,235,254,1) 0%, rgba(83,83,235,1) 100%); 
	display: flex;
	justify-content: center;
	align-content: center;
	height: 100vh;
}


main {
	width: 50%;
	display: flex;
	flex-direction: column;


	.input-section {
		margin: 2rem auto;
		text-align: center;

		h1 {
			font-family: $main-font;
		}

		form {
			margin-top: 0.7rem;

			input {
				width: 50%;
				border: 2px solid #f49a4b;
				border-radius: 1rem;
				padding: 0.5rem 1rem;
			}
			button {
				@include main-button()
			}
		}
	}

	.result-section {
		margin: 1rem auto;
		background-color: #f9f8f8;
		border: 3px solid #b0a2c9;
		border-radius: 15px;
		padding: 1rem;

		.city {
			display: flex;
			justify-content: space-between;

			.description {
				width: 50%;

			}
		}
	}
}

